import OpenFin from '@openfin/core';

import { getCurrentSync } from '@client-platform/api';

export const getBrowserWindow = async () => {
    const platform = getCurrentSync();
    const view = fin.View.wrapSync(fin.me.identity);
    const w = await view.getCurrentWindow();
    return platform.Browser.wrapSync(w.identity);
};

export const getBrowserWindowWithoutView = async (browserIdentity?: OpenFin.Identity) => {
    const platform = getCurrentSync();
    return platform.Browser.wrapSync(browserIdentity ?? fin.me.identity);
};
