import OpenFin from '@openfin/core';

import { getBrowserWindowWithoutView } from '@common/utils/get-browser-window';
import { trimUrl } from '@common/utils/url';
import { showAndFocus } from '@common/utils/window';
import { currentOFIdentity } from '@common/utils/window';

import { t } from '@common/api/i18next';
import { BookmarkNode } from '@common/api/pages/shapes';

import { getCurrentSync } from '@client-platform/api';
import { AttachedPage, Site } from '@client-platform/index';

export const COMMAND_BAR_HEIGHT = 40;
export const SEARCH_PROVIDER_TAB_HEIGHT = 33;

export const handleAlreadyOpenPage = (attachedPages: AttachedPage[], site?: Site): boolean => {
    if (!site || site?.type !== 'page' || !site?.id) {
        return false;
    }
    const pageId = site.id;
    for (const page of attachedPages) {
        if (pageId === page.pageId) {
            const bw = getCurrentSync().Browser.wrapSync(page.parentIdentity);
            showAndFocus(bw.identity);
            bw.setActivePage(site.id);
            return true;
        }
    }
    return false;
};

export const convertSiteData = (result: any): Site => {
    return {
        id: result.id,
        url: result.url,
        type: 'website',
        icon: result.icon,
        title: result.title
    };
};

export const getRecentlyVisitedSites = async () => {
    const platform = getCurrentSync();
    const browserWin = platform.Browser.wrapSync(currentOFIdentity);
    // eslint-disable-next-line no-underscore-dangle
    const getRecentlylVisited = await browserWin._getRecentlyVisitedSites();
    const recentlylVisitedSites: Site[] = [];
    getRecentlylVisited.forEach((result) => {
        recentlylVisitedSites.push(convertSiteData(result));
    });
    return recentlylVisitedSites;
};

export const formatRecentUrl = (url: string): string => {
    const { host, pathname, hash, search } = new URL(url);
    const [pathRoot, ...restOfPath] = pathname.split('/').filter((value) => value.length);
    if (restOfPath.length) {
        return `${host}/${pathRoot}/`;
    }
    return `${host}${pathname === '/' ? '' : pathname}${search}${hash}`;
};

export const getSiteSubTitle = (site: Site, urlFormatter: (value: string) => string): string => {
    const { type, subTitle } = site;
    if (subTitle) {
        return subTitle;
    }
    if (type === 'website') {
        const { url } = site;
        return urlFormatter(url);
    }
    const appOrSupertab =
        type === 'page'
            ? t('global.supertab')
            : type === 'collection'
            ? 'Collection'
            : type?.charAt(0).toUpperCase() + type?.slice(1);
    return appOrSupertab;
};

export const trimWithEllipsis = (str: string, characters: number) => {
    if (str.length > characters) {
        return `${str.substring(0, characters).trim()}`;
    } else {
        return str;
    }
};

export const checkViewUrl = async (
    view: OpenFin.View | null,
    url: string,
    setViewNavigatedToUrl: (value: boolean) => void
) => {
    if (!view) {
        setViewNavigatedToUrl(false);
        return;
    }

    const viewInfo = await view.getInfo();
    setViewNavigatedToUrl(trimUrl(viewInfo.url) === url);
};

export const searchBookmark = async (
    url: string,
    browserIdentity?: OpenFin.Identity
): Promise<BookmarkNode | undefined> => {
    const browserWindow = await getBrowserWindowWithoutView(browserIdentity);
    // eslint-disable-next-line no-underscore-dangle
    const potentialMatching = (await browserWindow._bookmarks._searchBookmarkNodes(trimUrl(url))) as BookmarkNode[];

    const bookmarks = potentialMatching.filter((bookmark) => {
        return trimUrl(bookmark.url) === trimUrl(url);
    });

    return bookmarks[0];
};
