import { useBaseUrl } from '@enterprise/utils/base-url-context';

import { AppSite } from '@client-platform/shapes';
import useSystemSchemeCheck from '@common/hooks/useSystemSchemeCheck';

import { CollectionIcon, PageIcon, SiteIcon, WorkspaceIcon } from './Icons';

const IconComponents = {
    app: SiteIcon,
    website: SiteIcon,
    page: PageIcon,
    workspace: WorkspaceIcon,
    collection: CollectionIcon
};

export type IconOptionsType = NonNullable<AppSite['options']>['icon'];

type ResultIconProps = {
    siteType: 'app' | 'website' | 'page' | 'workspace' | 'collection';
    icon: string;
    isSelected: boolean;
    iconOptions?: IconOptionsType;
};

export const ResultIcon = ({ siteType, icon, isSelected, iconOptions }: ResultIconProps) => {
    const { browserBaseUrl } = useBaseUrl();
    const { isSchemeDark } = useSystemSchemeCheck();
    const SpecificIcon = IconComponents[siteType] || SiteIcon; // Default to SiteIcon if no match, we don't have a Workspaces in Enterprise
    const isAppOrSite = siteType === 'website' || siteType === 'app';
    const iconProps = isAppOrSite ? { url: icon, iconOptions } : {};
    if ((isAppOrSite && Object.keys(iconProps).length === 0) || iconProps.url === '') {
        iconProps.url = `${browserBaseUrl}/enterprise/icons/default-favicon-enterprise-browser-${
            isSchemeDark ? 'dark' : 'light'
        }.svg`;
    }
    return <SpecificIcon {...iconProps} isSelected={isSelected} />;
};
