import { useEffect, useState } from 'react';

import { t } from '@common/api/i18next';

import { Site } from '@client-platform/shapes';

type SiteInfo = {
    title: string;
    url: string;
    icon: string;
};

export const useSiteInfo = (site: Site): SiteInfo => {
    const [siteInfo, setSiteInfo] = useState<SiteInfo>({ title: '', url: '', icon: '' });

    useEffect(() => {
        let newSiteInfo = { title: '', url: '', icon: '' };

        switch (site.type) {
            case 'website':
                newSiteInfo = {
                    ...newSiteInfo,
                    url: site.url,
                    title: site.title,
                    icon: site.icon
                };
                break;
            case 'app':
                newSiteInfo = {
                    ...newSiteInfo,
                    url: 'App',
                    title: `${site.title}`,
                    icon: site.icon
                };
                break;
            case 'page':
                newSiteInfo = { ...newSiteInfo, url: t('global.supertab'), title: `${site.title}` };
                break;
            case 'workspace':
                newSiteInfo = { ...newSiteInfo, url: 'Workspace', title: `${site.title}` };
                break;
            case 'collection':
                newSiteInfo = { ...newSiteInfo, url: 'Collection', title: `${site.title}` };
                break;
            default:
                break;
        }

        setSiteInfo(newSiteInfo);
    }, [site]);

    return siteInfo;
};
