import { useEffect, useState } from 'react';

import { Site } from '@client-platform/shapes';

const typeToTitleFormat = {
    app: (title: string) => `${title}`,
    website: (title: string) => title,
    page: (title: string) => `${title}`,
    workspace: (title: string) => `${title}`,
    collection: (title: string) => `${title}`
};
export const useSiteTitle = (site: Site) => {
    const [title, setTitle] = useState(null);

    useEffect(() => {
        if (site?.type) {
            const { type, title: siteTitle } = site;
            setTitle((typeToTitleFormat[type] || typeToTitleFormat['workspace'])(siteTitle));
        }
    }, [site]);

    return title;
};
