export const KeyIcon = ({ text }: { text: string }) => {
    const StyledText = styled(Text)`
        font-size: 9px;
        font-weight: 500;
        color: rgba(255, 255, 255, 0.5);
    `;

    return (
        <>
            <StyledText>{text}</StyledText>
        </>
    );
};

