import { useEffect, useState } from 'react';

import { getBrowserWindow } from '@common/utils/get-browser-window';

import { NavigationRequest, Site } from '@client-platform/shapes';

export const useCuratedContent = (): [Site[], (index: number) => Promise<void>] => {
    const [sites, setSites] = useState<Site[] | []>([]);

    useEffect(() => {
        const fetchSiteContent = async () => {
            const browserWin = await getBrowserWindow();
            // eslint-disable-next-line no-underscore-dangle
            const curatedContent = await browserWin._getCuratedContent();
            setSites(curatedContent);
        };
        fetchSiteContent();
    }, []);

    const handleSelectedSite = async (index: number) => {
        const site = sites[index];
        if (site) {
            const browserWin = await getBrowserWindow();
            const navigationRequest: NavigationRequest = {
                action: 'result-selected',
                windowIdentity: browserWin.identity,
                viewIdentity: fin.me.identity,
                site
            };
            // eslint-disable-next-line no-underscore-dangle
            browserWin._handleRequestNavigation(navigationRequest);
        }
    };

    return [sites, handleSelectedSite];
};
